import React, { Component } from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter, Route } from 'react-router-dom';
import Header from './common/header';
import Home from './pages/home';
import Detail from './pages/detail';
import Login from './pages/login';
import Write from './pages/write'
import store from './store';
class App extends Component {
  render() {
    return (
      <Provider store = {store} >
        <div>
          <BrowserRouter>
            <div>
              <Header />  
              <Route path='/jianshu' exact component={Home}></Route>
              <Route path='/jianshu/detail/:id' exact component={Detail}></Route>
              <Route path='/jianshu/login' exact component={Login}></Route>
              <Route path='/jianshu/write' exact component={Write}></Route>
            </div>
          </BrowserRouter>
        </div>
      </Provider>
    );
  }
}

export default App;



